.tinderCards__containerCard {
  position: relative;
  width: 36em;
  padding: 20px;
  max-width: 85vw;
  height: 50vh;
  max-height: 50vh;
  border-radius: 20px;
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
  box-shadow: 0px 18px 53px 0px rgba(0, 0, 0, 0.3);
}

.tinderCards__container {
  display: flex;
  justify-content: center;
  margin-top: 5vh;
}

.swipe {
  position: absolute;
}
h3 {
  position: absolute;
  bottom: 15px;
  color: #ffffff;
}
