.chat {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  height: 70px;
  border-bottom: 1px solid #fafafa;
}
a {
  text-decoration: none;
  color: inherit;
}
.chat__details {
  flex: 1;
  padding-left: 20px;
}
.chat__details h2 {
  font-size: 18px;
}
.chat__details > p {
  color: gray;
}
.chat__timestamp {
  color: gray;
}
